html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

.container {
  width: 95%;
  max-width: 1100px;
  margin: 0 auto;
}

img {
  max-width: 100%;
  display: block;
}

h1, h2, h3 {
  margin: 0;
  line-height: 1.2;
}

body {
  font-family: "Ubuntu", sans-serif;
  font-weight: 700;
}

h1 {
  font-family: "Overpass", sans-serif;
  font-size: 2rem;
}

p {
  font-family: "Overpass", sans-serif;
  font-weight: 400;
  font-size: 1.6rem;
  line-height: 1.8;
}

.header {
  background-image: url("../img/bg-pattern-intro.svg"), linear-gradient(135deg, #ff8f70 3%, #ff3d54 100%);
  background-repeat: no-repeat;
  background-size: 270%, cover;
  background-position: 35% 35%, center;
  position: relative;
  border-bottom-left-radius: 10rem;
}
@media (min-width: 768px) {
  .header {
    background-image: url("../img/bg-pattern-intro.svg"), linear-gradient(135deg, #ff8f70 15%, #ff3d54 100%);
    background-size: 200%, cover;
    background-position: 20% 50%, center;
  }
}
.header__top {
  padding: 5rem 2rem 0 2rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.header__logo {
  height: 4rem;
  object-fit: contain;
}
.header__button {
  border: none;
  background-color: transparent;
}
.header__button:hover {
  cursor: pointer;
}
@media (min-width: 768px) {
  .header__button {
    display: none;
  }
}

.navigation {
  font-size: 1.6rem;
  display: none;
}
@media (max-width: 767px) {
  .navigation.show {
    display: flex;
    position: absolute;
    flex-direction: column;
    background-color: white;
    border-radius: 0.5rem;
    width: 90%;
    top: 12rem;
    left: 5%;
    text-align: center;
    padding: 2rem 0;
    box-shadow: 0 2rem 5rem 0.5rem #87878c;
  }
}
.navigation.show__categories {
  border-bottom: 0.1rem solid #c8c8cb;
  width: 90%;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .navigation {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    padding-left: 4rem;
    width: 100%;
  }
}
@media (max-width: 767px) {
  .navigation__categories {
    width: 90%;
    margin: 0 auto;
    padding-bottom: 2rem;
    border-bottom: 0.1rem solid #c8c8cb;
  }
}
@media (min-width: 768px) {
  .navigation__categories {
    display: flex;
    flex-direction: row;
    align-items: center;
  }
}

.category {
  padding: 1rem 0;
}
@media (min-width: 768px) {
  .category {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 0 2rem;
  }
}
.category__mainLink {
  text-decoration: none;
  color: #4b5862;
  font-weight: 700;
  display: flex;
  justify-content: center;
  align-items: center;
}
@media (min-width: 768px) {
  .category__mainLink {
    color: white;
    flex-direction: row;
  }
}
.category__mainLink:hover {
  text-decoration: underline;
  cursor: pointer;
}
.category__mainLink::after {
  content: "";
  margin-left: 0.5rem;
  width: 1rem;
  height: 1rem;
  display: inline-block;
  background-image: url("../img/icon-arrow-dark.svg");
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center;
}
@media (min-width: 768px) {
  .category__mainLink::after {
    background-image: url("../img/icon-arrow-light.svg");
  }
}
.category__list {
  display: none;
  list-style: none;
  text-align: center;
  background-color: #c8c8cb;
  color: #4b5862;
  height: 0;
  padding: 0;
  margin: 1rem;
  border-radius: 0.5rem;
  overflow: hidden;
  transition: 0.5s;
}
.category__item {
  padding: 1rem 0;
}
.category__item a {
  text-decoration: none;
  color: #4b5862;
}
.category__item a:hover {
  color: #25252d;
}
.category.active .category__mainLink {
  color: #1f2428;
}
@media (min-width: 768px) {
  .category.active .category__mainLink {
    position: relative;
    color: white;
    text-decoration: underline;
  }
}
.category.active .category__mainLink::after {
  transform: rotate(180deg);
}
.category.active .category__list {
  display: flex;
  flex-direction: column;
  height: unset;
  padding: 1rem 0;
  margin: 2rem;
}
@media (min-width: 768px) {
  .category.active .category__list {
    background-color: white;
    position: absolute;
    top: 8.5rem;
    align-items: flex-start;
    padding: 1rem 4rem 1rem 2rem;
  }
}

.user {
  padding-top: 2rem;
  display: flex;
  flex-direction: column;
}
@media (min-width: 768px) {
  .user {
    flex-direction: row;
    padding-top: 0;
  }
}
.user__link {
  padding: 1rem 0;
  text-decoration: none;
  color: #4b5862;
  font-weight: 700;
}
.user__link:hover {
  text-decoration: underline;
}
@media (min-width: 768px) {
  .user__link {
    color: white;
  }
}
.user__link--sign {
  text-align: center;
  width: 12rem;
  margin: 1rem auto;
  padding: 1rem 2rem;
  border: 0.2rem solid white;
  border-radius: 2rem;
  background-image: linear-gradient(135deg, #ff8f70 15%, #ff3d54 100%);
  color: white;
}
@media (min-width: 768px) {
  .user__link--sign {
    background-image: linear-gradient(to bottom, white 0% 100%);
    color: #ff3d54;
    margin: 0 0 0 2rem;
  }
}
.user__link--sign:hover {
  background-image: linear-gradient(to bottom, #ff7a85 0% 100%);
  border: 0.2rem solid #ff7a85;
  color: white;
  text-decoration: none;
}

.hero {
  font-size: 1.6rem;
  padding: 10rem 0 15rem 0;
  text-align: center;
  color: white;
}
@media (min-width: 768px) {
  .hero {
    padding: 15rem 0 20rem 0;
  }
}
.hero__title {
  font-size: 3.7rem;
  padding: 0 1rem;
}
@media (min-width: 768px) {
  .hero__title {
    font-size: 7rem;
  }
}
.hero__text {
  font-size: 1.8rem;
  padding: 0 1.5rem;
  margin-bottom: 4rem;
}
.hero__link {
  text-decoration: none;
  margin: 0 0.3rem;
  padding: 1rem 2rem;
  border: 0.2rem solid white;
  border-radius: 2rem;
}
.hero__link:nth-child(1) {
  background-color: white;
  color: #ff3d54;
}
.hero__link:nth-child(1):hover {
  background-color: #ff7a85;
  border: 0.2rem solid #ff7a85;
  color: white;
}
.hero__link:nth-child(2) {
  color: white;
}
.hero__link:nth-child(2):hover {
  background-color: white;
  color: #ff3d54;
}

.future {
  padding: 10rem 0;
  text-align: center;
  overflow: hidden;
}
@media (min-width: 768px) {
  .future {
    text-align: left;
    position: relative;
  }
}
@media (min-width: 1440px) {
  .future {
    min-height: 85rem;
  }
}
.future__heading {
  text-align: center;
  font-size: 2.5rem;
  color: #1f3f5b;
  margin-bottom: 2rem;
}
@media (min-width: 768px) {
  .future__heading {
    font-size: 4rem;
    font-weight: 500;
  }
}
@media (min-width: 768px) {
  .future__grid {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-auto-flow: dense;
  }
  .future__grid :nth-child(1) {
    grid-column: 2/3;
  }
}
.future__img {
  margin: 0 auto;
}
@media (min-width: 768px) {
  .future__img {
    position: absolute;
    width: 60rem;
    top: 10rem;
    right: -20rem;
  }
}
@media (min-width: 1440px) {
  .future__img {
    width: 80rem;
    top: 5rem;
    right: -25rem;
  }
}
.future__info {
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.future__block {
  padding-right: 4.8rem;
}
@media (min-width: 768px) {
  .future__block:first-of-type {
    padding-top: 2rem;
    margin: 4rem 0;
  }
}
.future__title {
  font-size: 2.5rem;
  color: #1f3f5b;
  padding: 3rem 3rem 1rem 3rem;
}
@media (min-width: 768px) {
  .future__title {
    padding: 1rem 5rem 1rem 0;
  }
}
.future__description {
  padding: 0 3rem 1rem 3rem;
}
@media (min-width: 768px) {
  .future__description {
    margin: 0;
    padding: 1rem 5rem 1rem 0;
  }
}

.infraestructure {
  background-image: url("../img/bg-pattern-circles.svg"), linear-gradient(to bottom, #37384e 0% 100%);
  background-repeat: no-repeat;
  background-size: 150%, 100%;
  background-position: top -22rem left 50%, center;
  color: white;
  margin: 10rem 0;
  border-top-right-radius: 10rem;
  border-bottom-left-radius: 10rem;
}
@media (min-width: 768px) {
  .infraestructure {
    background-size: 102rem, 100%;
    background-position: top -60rem left -30rem, center;
  }
}
@media (min-width: 1440px) {
  .infraestructure {
    background-position: top -50rem left -22rem, center;
  }
}
.infraestructure__grid {
  position: relative;
}
@media (min-width: 768px) {
  .infraestructure__grid {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
  }
}
.infraestructure__img {
  position: absolute;
  width: 90%;
  top: -15rem;
}
@media (min-width: 768px) {
  .infraestructure__img {
    width: 50rem;
    top: -5rem;
    left: -4rem;
  }
}
@media (min-width: 1440px) {
  .infraestructure__img {
    width: 60rem;
    top: -8rem;
    left: -5rem;
  }
}
.infraestructure__info {
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: center;
  padding: 65% 0 10rem 0;
}
@media (min-width: 768px) {
  .infraestructure__info {
    text-align: left;
    padding: 7rem 0 7rem 7rem;
  }
}
@media (min-width: 1440px) {
  .infraestructure__info {
    padding: 10rem 0;
  }
}
.infraestructure__heading {
  font-size: 3.6rem;
  font-weight: 300;
  margin-bottom: 0.5rem;
}
.infraestructure__text {
  padding: 0 3rem;
}
@media (min-width: 768px) {
  .infraestructure__text {
    padding: 0 3rem 0 0;
  }
}

.laptop {
  padding: 0 0 10rem 0;
}
@media (min-width: 768px) {
  .laptop {
    padding: 0 0 15rem 0;
    position: relative;
  }
}
@media (min-width: 1440px) {
  .laptop {
    padding: 15rem 0 20rem 0;
  }
}
@media (min-width: 768px) {
  .laptop__grid {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
  }
}
.laptop__img {
  margin: 0 auto;
}
@media (min-width: 768px) {
  .laptop__img {
    position: absolute;
    left: -20rem;
    width: 65rem;
  }
}
@media (min-width: 1440px) {
  .laptop__img {
    top: 0;
    left: -24rem;
    width: 85rem;
  }
}
.laptop__info {
  text-align: center;
}
@media (min-width: 768px) {
  .laptop__info {
    text-align: left;
  }
}
.laptop__block {
  padding: 0 3rem;
  color: #4b5862;
}
@media (min-width: 768px) {
  .laptop__block {
    padding: 0 5rem 0 0;
  }
}
.laptop__title {
  color: #1f3f5b;
  font-weight: 500;
  font-size: 2.7rem;
  padding: 2.5rem 0 1rem 0;
}

.footer {
  background-color: #25252d;
  border-top-right-radius: 10rem;
  padding: 5rem 0;
  font-size: 1.6rem;
  text-align: center;
}
@media (min-width: 768px) {
  .footer {
    text-align: left;
    padding: 8rem 0;
  }
}
.footer__img {
  margin: 0 auto;
}
@media (min-width: 768px) {
  .footer__img {
    margin: 0;
  }
}
@media (min-width: 768px) {
  .footer__grid {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
  }
}
.footer__list {
  list-style: none;
  padding: 1rem 0;
  margin: 0;
  color: white;
}
@media (min-width: 768px) {
  .footer__list {
    padding: 0;
  }
}
.footer__item {
  font-weight: 300;
  padding: 0.7rem;
}
@media (min-width: 768px) {
  .footer__item {
    padding: 1rem 0;
  }
}
.footer__item:first-of-type {
  color: white;
  font-weight: 700;
  padding: 4rem 0 2rem 0;
}
@media (min-width: 768px) {
  .footer__item:first-of-type {
    padding: 1rem 0 3.5rem 0;
  }
}
.footer__item a {
  color: #c8c8cb;
  text-decoration: none;
}
.footer__item a:hover {
  text-decoration: underline;
}